.main {
  padding: 6rem 4rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 110px;
  align-items: center;
}

.main img {
  border-radius: 0.5rem;
}

@media (max-width: 1150px) {
  .main {
    grid-template-columns: 1fr;
    gap: 55px;
  }
}

@media (max-width: 60rem) {
  .main {
    padding: 0;
    gap: 10px;
  }
}
